*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.main_div{
    min-height: 650px;
    background-color: rgb(114, 124, 128);
    display: flex;
    justify-content: center;
    padding-top: 20px;
  padding-bottom: 30px;
    
}
.center_div{
   width: 400PX;
    background-color: rgb(238, 238, 238);
    text-align: center;
  border-radius: 10PX;
}
input{
    width: 250PX;
    background-color: rgb(238, 238, 238);
    border-style: none;
    text-align: center;
    outline: none;
    font-size: 1.5rem;
    border-bottom-style: solid;
    margin-bottom: 20px;
    
}
H1{
    background-color:rgb(131, 29, 109);
    padding: 20PX 20PX;
    color: wheat;
}
button{
    width: 40px;
    height: 40px;
    border-radius: 30px;
    background-color: rgb(131, 29, 109);
    font-size: 1.8rem;
    color: wheat;
    outline: none;
    margin-left: 10PX;
    border-style: none;
    cursor: pointer;
    font-style: normal;
    font-family: initial;
    
    
}
button:hover {
    background-color: green;
    color: black;
}
ol{
    list-style:none;
}
li{  padding-left: 40px;
    text-align: left;
    font-size: 1.5rem;
    font-family:Verdana, Geneva, Tahoma, sans-serif ;
    color: rgb(0, 0, 0);
    margin: 10px ;
}
.fas{
    color:rgb(131, 29, 109) ;
    }
    .fas:hover{
        color:rgb(255, 17, 0) ;
        }
@media  (max-width:450px) {
    input{
        width: 250PX;
        background-color: rgb(238, 238, 238);
        border-style: none;
        text-align: center;
        outline: none;
        font-size: 1.5rem;
        border-bottom-style: solid;
      
    }
    h1{
        font-size: 1.3rem;
    }
    li{
        font-family: Verdana, Geneva, Tahoma, sans-serif;
    }
}